// app/page.tsx
import React from 'react';
import { getAllProducts } from './lib/products';
import { applyFilters, Opts } from './lib/util';
import ProductCard from './components/ProductCard';
import ControlsView from './components/ControlsView';
import ShopClient from './components/ShopClient';
import './shop.css'

export default function Page() {
  const products = getAllProducts();

  const initialOpts: Opts = {
    minPrice: 0,
    maxPrice: 9999999,
    sort: 'default',
    perPage: 12,
    page: 1,
  };

  const initialPage = applyFilters(products, initialOpts);

  return (
    <main>
      {/* 服务器端渲染的 wrapper: 包含静态控件和初始 product list */}
      <div className="shop-wrapper container-grid">
        <aside className="filters-area">
          <ControlsView opts={initialOpts} />
        </aside>

        <section className="product-area">
          <div id="product-list" className="product-grid">
            {initialPage.items.map((p) => (
              <ProductCard key={p.id} p={p} />
            ))}
          </div>

          <div id="pagination" className="pagination-wrap">
            {/* 简单 server-side pagination rendering */}
            {initialPage.totalPages > 1 && (
              <nav className="pagination">
                {Array.from({ length: initialPage.totalPages }, (_, i) => {
                  const n = i + 1;
                  return (
                    <button key={n} className={`page-btn${n === initialPage.page ? ' active' : ''}`} aria-current={n === initialPage.page ? 'page' : undefined}>
                      {n}
                    </button>
                  );
                })}
              </nav>
            )}
          </div>
        </section>
      </div>

      {/* 客户端入口（hydration + 交互）。传入相同的数据以确保初次渲染一致 */}
      <ShopClient allProducts={products} initialOpts={initialOpts} initialPageResult={initialPage} />
    </main>
  );
}